// src/pages/AboutPage.jsx
import React from 'react';
import styled from 'styled-components';
import { useNavigate } from 'react-router-dom';

// ---------------- STYLED COMPONENTS ----------------
const PageContainer = styled.div`
  padding: 40px 30px;
  min-height: 100vh;
  background-color: ${({ theme }) => theme.bg};
  color: ${({ theme }) => theme.text_primary};
  display: flex;
  flex-direction: column;
  gap: 30px;

  @media (max-width: 768px) {
    padding: 20px 10px;
  }
`;

const PageTitle = styled.h1`
  font-size: 36px;
  color: rgb(244, 187, 68);
  text-align: center;
`;

const Section = styled.div`
  background-color: ${({ theme }) => theme.card_bg || "#1e1e1e"};
  padding: 25px 20px;
  border-radius: 10px;
  line-height: 1.7;
`;

const SectionTitle = styled.h2`
  color: rgb(228,155,15);
  font-size: 24px;
  margin-bottom: 15px;
`;

const SectionText = styled.p`
  font-size: 16px;
  color: ${({ theme }) => theme.text_primary};
  margin-bottom: 10px;
`;

const BackButton = styled.button`
  align-self: center;
  padding: 10px 20px;
  background-color: rgb(244, 187, 68);
  color: #000;
  font-weight: bold;
  font-size: 16px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.2s;

  &:hover {
    background-color: rgb(228,155,15);
  }
`;

// ---------------- COMPONENTE ----------------
const AboutPage = () => {
  const navigate = useNavigate();

  return (
    <PageContainer>
      <PageTitle>Informacin del Sitio Web</PageTitle>

      <Section>
        <SectionTitle>Propsito del Sitio</SectionTitle>
        <SectionText>
          Este sitio web fue creado para ofrecer informacin educativa sobre arquitecturas de aplicaciones mviles, patrones de diseo y buenas prcticas de desarrollo.
        </SectionText>
        <SectionText>
          Incluye artculos, podcasts generados con IA y diagramas explicativos que facilitan la comprensin de conceptos complejos en desarrollo mvil.
        </SectionText>
      </Section>

      <Section>
        <SectionTitle>Pblico Objetivo</SectionTitle>
        <SectionText>
          Est dirigido a estudiantes, desarrolladores junior y cualquier persona interesada en aprender sobre la organizacin de aplicaciones mviles modernas y el uso de tecnologas emergentes.
        </SectionText>
      </Section>

      <Section>
        <SectionTitle>Contenido Destacado</SectionTitle>
        <SectionText>
          - Modelos arquitectnicos: MVC, MVP, MVVM, Clean Architecture.  
          - Integracin con servicios en la nube.  
          - Almacenamiento local y sincronizacin con la nube.  
          - Escalabilidad mediante microservicios.  
          - Retos de tecnologas emergentes: IA, IoT y RA.
        </SectionText>
      </Section>

      <Section>
        <SectionTitle>Contacto y Crditos</SectionTitle>
        <SectionText>
          Para consultas o comentarios, puedes contactar al desarrollador a travs del correo electrnico o redes sociales proporcionadas en la pgina principal.  
          Los recursos visuales y podcasts fueron generados para fines educativos.
        </SectionText>
      </Section>

      <BackButton onClick={() => navigate(-1)}>Volver atrs</BackButton>
    </PageContainer>
  );
};

export default AboutPage;
