import React from 'react';
import styled from "styled-components";
import { Link } from 'react-router-dom';
import PodcastCard from '../components/PodcastCard';

import mvcImage from '../images/mvc.jpg';
import mvpImage from '../images/mvp.jpg';
import mvvmImage from '../images/mvvm.jpg';
import cleanImage from '../images/clean.jpg';

// ---------------- STYLED COMPONENTS ----------------
const InfoMain = styled.div`
  padding: 20px 30px;
  padding-bottom: 100px;
  height: 100%;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
  gap: 40px;

  @media (max-width: 768px) {
    padding: 6px 10px;
  }
`;

const ThemeSection = styled.div`
  background-color: ${({ theme }) => theme.bg};
  border-radius: 10px;
  padding: 30px;
`;

const SectionTitle = styled.div`
  color: rgb(244, 187, 68);
  font-size: 28px;
  font-weight: bold;
  text-align: center;
  margin-bottom: 20px;
`;

const SectionDescription = styled.div`
  color: ${({ theme }) => theme.text_primary};
  font-size: 16px;
  line-height: 1.6;
  text-align: justify;
  margin-bottom: 30px;
`;

const CardsContainer = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 20px;
`;

const Card = styled.div`
  background-color: ${({ theme }) => theme.card_bg || "#1e1e1e"};
  border-radius: 10px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 12px;
  transition: transform 0.2s;
  &:hover {
    transform: translateY(-5px);
  }
`;

const CardImage = styled.img`
  width: 120px;
  height: 120px;
  object-fit: cover;
  border-radius: 8px;
  margin-bottom: 10px;
`;

const CardTitle = styled.h4`
  color: rgb(228,155,15);
  margin-bottom: 5px;
  text-align: center;
`;

const CardDescription = styled.p`
  color: #aaa;
  font-size: 14px;
  text-align: center;
  margin-bottom: 10px;
`;

const ViewMoreButton = styled(Link)`
  padding: 8px 14px;
  background-color: rgb(244, 187, 68);
  color: #000;
  border-radius: 5px;
  text-decoration: none;
  font-weight: bold;
  font-size: 14px;
  transition: background-color 0.2s;
  &:hover {
    background-color: rgb(228,155,15);
  }
`;

// ---------------- DATOS ----------------
const themeData = [
  {
    title: "Modelos arquitectnicos ms comunes (MVC, MVP, MVVM, Clean Architecture)",
    description: "Exploramos los patrones de diseo ms usados en aplicaciones modernas y cmo ayudan a organizar cdigo, separar responsabilidades y mejorar mantenibilidad.",
    cards: [
      { title: "MVC", description: "Separacin de Modelo, Vista y Controlador para mantener el cdigo organizado.", image: mvcImage, link: "/architecture/mvc" },
      { title: "MVP", description: "Presenter maneja la lgica y comunica Modelo y Vista.", image: mvpImage, link: "/architecture/mvp" },
      { title: "MVVM", description: "ViewModel expone datos mediante bindings para la Vista.", image: mvvmImage, link: "/architecture/mvvm" },
      { title: "Clean Architecture", description: "Separacin en capas desacopladas que facilitan pruebas y mantenimiento.", image: cleanImage, link: "/architecture/clean" }
    ]
  },
  {
    title: "Integracin con servicios en la nube",
    description: "Las apps mviles modernas dependen de la nube para almacenamiento, autenticacin, notificaciones y lgica de negocio. Plataformas como AWS, Google Cloud y Azure permiten integrar: bases de datos en tiempo real, almacenamiento, funciones serverless y autenticacin segura.",
    cards: [
      { title: "Integracin Cloud", description: "Uso de APIs y SDKs para conectar la app con backend, almacenamiento y servicios cloud de forma escalable y segura.", link: "/cloud-integration" }
    ]
  },
  {
    title: "Uso de almacenamiento local y sincronizacin con la nube",
    description: "Para mantener funcionalidad offline, se deben usar estrategias como colas de operaciones, control de conflictos mediante timestamps y sincronizacin bidireccional, garantizando consistencia de datos y experiencia fluida.",
    cards: [
      { title: "Almacenamiento + Sincronizacin", description: "Tcnicas para manejar datos locales y sincronizarlos automticamente con la nube al reconectar.", link: "/local-sync-cloud" }
    ]
  },
  {
    title: "Escalabilidad y microservicios en aplicaciones mviles modernas",
    description: "Disear el backend con microservicios permite escalar funcionalidades especficas sin afectar toda la app. Se recomienda usar gateways, circuit breakers, autenticacin centralizada y patrones de abstraccin en el frontend para un mantenimiento eficiente.",
    cards: [
      { title: "Microservicios y Escalabilidad", description: "Arquitectura que facilita crecimiento, resiliencia y alta disponibilidad de la aplicacin.", link: "/scalability-microservices" }
    ]
  },
  {
    title: "Retos arquitectnicos con tecnologas emergentes (IA, IoT, realidad aumentada)",
    description: "Integrar IA, IoT y RA presenta desafos: consumo de recursos, latencia, compatibilidad de dispositivos y seguridad de datos. Se recomienda modularidad, procesamiento hbrido (local y nube) y patrones reactivos para optimizar el rendimiento.",
    cards: [
      { title: "Tecnologas Emergentes", description: "Cmo enfrentar retos de rendimiento, compatibilidad y seguridad al usar IA, IoT y RA.", link: "/emerging-tech-challenges" }
    ]
  },
  {
    title: "Informacin Adicional",
    description: "Esta pgina fue creada como parte de la actividad acadmica sobre arquitecturas de aplicaciones mviles. Incluye un podcast generado con IA y diagramas de las principales arquitecturas utilizadas en desarrollo mvil, adems de recomendaciones y referencias para profundizar en cada patrn y tecnologa.",
    cards: [
      { title: "Actividad Acadmica", description: "Contenido educativo para aprendizaje de arquitecturas mviles y buenas prcticas de desarrollo." }
    ]
  }
];

// ---------------- COMPONENTE ----------------
const ArchitectureThemes = () => {
  return (
    <InfoMain>
      {/* SECCIN DE PODCAST */}
      <ThemeSection>
        <SectionTitle>Podcast</SectionTitle>
        <SectionDescription>
          Escucha episodios relacionados con arquitectura de aplicaciones mviles y patrones de diseo.
        </SectionDescription>
        <PodcastCard />
      </ThemeSection>

      {/* OTRAS SECCIONES */}
      {themeData.map((theme, themeIndex) => (
        <ThemeSection key={themeIndex}>
          <SectionTitle>{theme.title}</SectionTitle>
          <SectionDescription>{theme.description}</SectionDescription>
          <CardsContainer>
            {theme.cards.map((card, cardIndex) => (
              <Card key={cardIndex}>
                {/* Mostrar imagen solo si existe */}
                {card.image && <CardImage src={card.image} alt={card.title} />}
                <CardTitle>{card.title}</CardTitle>
                <CardDescription>{card.description}</CardDescription>
                {/* Solo mostrar botn 'Ver ms' si existe el link */}
                {card.link && <ViewMoreButton to={card.link}>Ver ms</ViewMoreButton>}
              </Card>
            ))}
          </CardsContainer>
        </ThemeSection>
      ))}
    </InfoMain>
  );
};

export default ArchitectureThemes;
